import React from "react";

import L from "leaflet";
import markerIcon from "leaflet/dist/images/marker-icon.png";
import markerShadow from "leaflet/dist/images/marker-shadow.png";
import {MapContainer, Marker, Popup, TileLayer} from "react-leaflet";

import "./Map.scss";
import "leaflet/dist/leaflet.css";

delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
    iconUrl: markerIcon,
    shadowUrl: markerShadow,
});

// eslint-disable-next-line valid-jsdoc
/**
 * @returns
 */
function DarkMap () {
    const position = [40.7128, -74.0060]; // New York City

    return (
        <MapContainer options={{fadeAnimation: false, zoomAnimation: false}}
            center={position} zoom={12}
            style={{height: "100vh", width: "100%", backgroundColor: "#1e1e1e"}}>
            <TileLayer
                attribution='&copy; <a href="https://carto.com/">CartoDB</a>'
                url="https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png"
            />
            <Marker position={position}>
                <Popup>Dark mode map using CartoDB tiles.</Popup>
            </Marker>
        </MapContainer>
    );
}

export default DarkMap;
